    text-align: center;
    background-color: #212529;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
}
*{
    padding:0;
    margin:0;
}
.navWeb {
  color: #dadada;
  font: 22px "open sans condensed", sans-serif;
  font-weight: bold;
  padding-top: 60px;
}
.navWebDropDown {
  color: #ffbf40;
  font: 22px "open sans condensed", sans-serif;
  font-weight: bold;
  padding-top: 55px;
  margin-top: -2px;
}
.navWebFather{
    margin-left: 150px;
}

.navbar-light .navbar-nav .nav-link {
  color: #ffbf40;
}
.navbar-nav {
  padding-right: 550px;
}

.bg-dark {
  background-color: #ffffff !important;
}
.logo {
  width: 170px;
  height: 30px;
  margin-left: 500px;
  margin-top: 45px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: start;
          justify-content: start;
}

.navbar-dark .navbar-nav .nav-link:focus,
.navbar-dark .navbar-nav .nav-link:hover {
  color: rgb(0 0 0 / 75%);
}
.nav {
  position: fixed;
  width: 100%;
  z-index: 100;
  transition: all 0.5s;
}

.nav-mobile {
  display: none;
}

@media screen and (max-width: 991px) {
  .nav {
    bottom: 0;
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }

  .navWebFather {
    display: none;
  }
  .nav-mobile {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
  }
  .navbar-nav {
      width: 100%;
      padding-right: 0px;
  }
  .nav-mobile img{
      width: 40px;
  }
  .brand{
      display: none;
  }
}

